<template>
    <a-tabs type="card" :activeKey ="tabkey" @change="changeTbas">
        <a-tab-pane tab="" key="1">
            <div class="table_warrap">
                <a-table bordered 
                :dataSource="catelistdata"
                size="small" 
                :rowKey="record => record.id" 
                :columns="columns">
                    <template slot="title">
                        <div style="display:flex">
                        <div style="flex:1"></div>
                        <div style="flex:1">
                            <a-button style="float:right" size="small" type="primary" @click="addStart('cate')"></a-button>
                        </div>
                        </div>
                    </template>
                    <template slot="operation" slot-scope="text, record">
                        <div style="text-align:center">
                            <a-popconfirm
                            title="?"
                            okText=""
                            cancelText=""
                            @confirm="() => onDelete(record.key)"
                            >
                            <a-button size="small" style="margin-right:7px;"></a-button>
                            </a-popconfirm>
                            <a-button size="small" type="primary" @click="editeStart(record)"></a-button>
                        </div>
                    </template>
                </a-table>
                <a-modal v-model="edite_visible" title="Title" onOk="editeSubmit">
                    <template slot="footer">
                        <a-button key="back" @click="editeCancel"></a-button>
                        <a-button key="submit" type="primary" :loading="edite_loading" @click="editeSubmit">
                            
                        </a-button>
                    </template>
                    <form class="edite_cate">
                        <div class="input_line">
                            <label class="label_name">:</label>
                            <a-input placeholder="" v-model="edite_cate_data.catename" />
                        </div>
                        <div class="input_line">
                            <label class="label_name">:</label>
                            <a-input placeholder="" v-model="edite_cate_data.descs"/>
                        </div>
                        <div class="input_line">
                            <label class="label_name">id():</label>
                            <a-input placeholder="0" disabled v-model="edite_cate_data.pid" />
                            <a-select 
                            v-model="edite_cate_data.pid" 
                            style="margin-left:7px;width:300px">
                                <a-select-option :value="0" title=""></a-select-option>
                                <a-select-option :value="item.id" :title="item.catename" v-for="item in catelistdata" :key="item.id">{{item.catename}}</a-select-option>
                            </a-select>
                        </div>
                        <div class="input_line">
                            <label class="label_name">:</label>
                            <a-input placeholder="" v-model="edite_cate_data.path" />
                        </div>
                    </form>
                </a-modal>
            </div>
        </a-tab-pane>
        <a-tab-pane tab="" key="2">
            <form class="cate_wrapper">
                <div class="input_line">
                    <label class="label_name">:</label>
                    <a-input placeholder="" v-model="catedata.catename" />
                </div>
                <div class="input_line">
                    <label class="label_name">:</label>
                    <a-input placeholder="" v-model="catedata.descs"/>
                </div>
                <div class="input_line">
                    <label class="label_name">id():</label>
                    <a-input placeholder="0" disabled v-model="catedata.pid" />
                    <a-select v-model="catedata.pid" defaultValue="0" style="margin-left:7px;width:300px">
                        <a-select-option :value="0" title=""></a-select-option>
                        <a-select-option :value="item.id" :title="item.catename" v-for="item in catelistdata" :key="item.id">{{item.catename}}</a-select-option>
                    </a-select>
                </div>
                <div class="input_line">
                    <label class="label_name">:</label>
                    <a-input placeholder="" v-model="catedata.path" />
                </div>
                <div class="input_line">
                    <div class="btn_div">
                        <a-button type="primary" style="margin-right:7px;" @click="addCate"></a-button>
                        <a-button @click="resetData(catedata)"></a-button>
                    </div>
                </div>
            </form>
        </a-tab-pane>
        <a-tab-pane tab="" key="3">
            <a-table bordered 
                :dataSource="tagdatalist"
                size="small" 
                :rowKey="record => record.id" 
                :columns="tagscolumns">
                    <template slot="title">
                        <div style="display:flex">
                        <div style="flex:1">TAGS</div>
                        <div style="flex:1">
                            <a-button style="float:right" size="small" type="primary" @click="addStart('tags')"></a-button>
                        </div>
                        </div>
                    </template>
                    <template slot="operation" slot-scope="text, record">
                        <div style="text-align:center">
                            <a-popconfirm
                            title="?"
                            okText=""
                            cancelText=""
                            @confirm="() => onDelete(record.key)"
                            >
                            <a-button size="small" style="margin-right:7px;"></a-button>
                            </a-popconfirm>
                            <a-button size="small" type="primary" @click="editeStart(record,'tags')"></a-button>
                        </div>
                    </template>
            </a-table>
            <a-modal v-model="edite_visibles" title="tag" onOk="editeTagSubmit">
                <template slot="footer">
                    <a-button key="back" @click="editeCancel"></a-button>
                    <a-button key="submit" type="primary" :loading="edite_loadings" @click="editeTagSubmit">
                        
                    </a-button>
                </template>
                <form class="edite_cate">
                    <div class="input_line">
                        <label class="label_name">:</label>
                        <a-input placeholder="" v-model="edite_tags_data.tagname" />
                    </div>
                </form>
            </a-modal>
        </a-tab-pane>
        <a-tab-pane tab="" key="4">
            <form class="cate_wrapper">
                <div class="input_line">
                    <label class="label_name">:</label>
                    <a-input placeholder="" v-model="tagdata.tagname" />
                </div>
                <div class="input_line">
                    <div class="btn_div">
                        <a-button type="primary" style="margin-right:7px;" @click="addTags"></a-button>
                        <a-button @click="resetData(tagdata)"></a-button>
                    </div>
                </div>
            </form>
        </a-tab-pane>
    </a-tabs>
</template>
<script>
import {getCate,addCate,updateCate,addTags,getTagsList,updataTags} from '../../apicontrol/apilist.js'
export default {
  data() {
    return {
        edite_loading: false,
        edite_loadings:false,
        edite_visible: false,
        edite_visibles:false,
        edite_cate_data:{
            catename:'',
            path:'',
            descs:'',
            pid:''
        },
        catelistdata: [],
        tagdatalist:[],
        tagscolumns:[
            {
                title: "ID",
                dataIndex: "id",
            },
            {
                title: "",
                dataIndex: "tagname"
            },
            {
                title: "",
                dataIndex: "operation",
                scopedSlots: { customRender: "operation" }
            }
        ],
        columns: [
            {
                title: "ID",
                dataIndex: "id",
            //scopedSlots: { customRender: "name" }
            },
            {
                title: "",
                dataIndex: "catename"
            },
            {
                title: "",
                dataIndex: "pid"
            },
            {
                title: "",
                dataIndex: "descs"
            },
            {
                title: "",
                dataIndex: "path"
            },
            {
                title:"",
                dataIndex:"is_delete",
            },
            {
            title: "",
                dataIndex: "operation",
                scopedSlots: { customRender: "operation" }
            }
        ],
        catedata:{
            catename:'',
            path:"",
            descs:"",
            pid:"0",
        },
        tagdata:{
            tagname:''
        },
        edite_tags_data:{
            tagname:""
        },
        tabkey:"1",
    };
  },
  created() {
      this.getCateList();
      this.getTagsList();
      this.test();
  },
  methods: {
    onDelete(key) {
      const dataSource = [...this.dataSource];
      this.dataSource = dataSource.filter(item => item.key !== key);
    },
    //
    addStart(type){
        switch (type) {
            case 'cate':
                this.tabkey = "2"
                break;
            case 'tags':
                this.tabkey = "4"
                break;
            default:
                this.tabkey = "1"
                break;
        }
    },
    addCate() {
        let that = this;
        addCate(this.catedata).then(res=>{
            that.$message.success(res.message);
        })
    },
    addTags(){
        let that = this;
        addTags(this.tagdata).then(res=>{
            that.$message.success(res.message);
        })
    },
    resetData(data){
        for(let item in data){
            data[item] = ""
        }
    },
    getCateList(){
        getCate().then(res=>{
            this.catelistdata = res.data;
        })
    },
    getTagsList(){
        getTagsList().then(res=>{
            this.tagdatalist = res.data;
        })
    },
    //
    editeCancel(){

    },
    //
    editeStart(data,type="cate"){
        if(type=="tags"){
            this.edite_visibles = true
            this.edite_tags_data = data;
        }else{
            this.edite_visible = true;
            this.edite_cate_data = data;
        }
    },
    //
    async editeSubmit(){
        let that = this;
        this.edite_loading = true;
        await setTimeout(() => {
          this.edite_loading = false;
          this.edite_visible = false;
        }, 2000);
        updateCate(this.edite_cate_data).then(res=>{
            that.$message.success(res.message);
        })
    },
    async editeTagSubmit(){
        let that = this;
        this.edite_loadings = true;
        await setTimeout(()=>{
            this.edite_loadings = false;
            this.edite_visibles = false
        },2000);
        updataTags(this.edite_tags_data).then(res=>{
            that.$message.success(res.message);
        })
    },
    //tabs
    changeTbas(key){
        this.tabkey = key;
    },
    //
    test(){
        
    }
  }
};
</script>
<style lang="less" scoped>
.table_warrap{
    overflow: auto;
    height: 100%;
}
.cate_wrapper{
    padding:0 10%;
    .input_line{
        display: flex;
        margin-bottom: 7px;
        .btn_div{
            width: 100%;
            text-align: right;
        }
    }
    .label_name{
        height:32px;
        line-height: 32px;
        font-weight: 500;
        flex: 0 0 200px;
    }
}
.edite_cate{
    padding:0 10%;
    .input_line{
        display: flex;
        margin-bottom: 7px;
    }
    .label_name{
        height:32px;
        line-height: 32px;
        font-weight: 500;
        flex: 0 0 100px;
    }
}
</style>